@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #783E04;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#timeline_box {
    background-color: #ECF9F6;
    width: 80%;
    margin: auto;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 5em;
}

.timeline-text{
    font-family: 'bitter', serif;
    color: #783E04;
}
#timeline-unit{
    margin: 80px;
}
#timeline-info{
    width: 80%;
    margin: auto;
}
#header{
    font-weight: 600;
}
#explainer{
    border-bottom: 2px #783E04 solid;
    padding-bottom: 10px
}
#sources {
    border-top: 2px #783E04 solid
}
#byline, #sources{
    font-size: 12px;
    margin: 0px
}

@media (max-width: 600px) {
    #timeline_box {
        background-color: #ECF9F6;
        width: 100%;
        margin: auto;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-top: 5em;
    }
    #timeline-unit{
        margin: 0px;
    }
}
.map_container {
    height: 100%;
    align-items: center;
    margin: auto;
    padding-bottom: 10px;
    line-height: 1.5em;
}
.leaflet-container {
    width: 80%;
    height: 500px;
    margin: auto;
    margin-top: 1.5em;
    align-items: center;
    padding: 10px;
  }
.map-text{
    font-family: 'bitter', serif;
    color: white;
    margin: auto;
    width: 80%;

}
#header{
    padding-bottom: 10px;
    font-weight: 600;
}
#explainer{
    border-bottom: 2px #783E04 solid;
    padding-bottom: 10px;
}
#sources {
    border-top: 2px #783E04 solid;
    margin: auto;
}
#byline, #sources{
    font-size: 12px;
    margin: auto;
}
 
.popup{
    font-family: 'bitter', serif;
    margin: 0px;
}
#popup-header {
    font-weight: bold; 
    margin: 0px;
}


@media (max-width: 600px) {
    #map{
        width: 160%;
        margin-left: -25%;
    }
    
}
